<template>
  <BaseLayout title="我的卡券">
    <template #extra>
      <van-button icon="cash-back-record" :style="{width:'110px', borderRadius:'10px 0 0 10px', color: '#000'}" color="#F6F6F6" @click="state.popup=true">领取</van-button>
    </template>

    <van-dropdown-menu>
      <van-dropdown-item v-model="state.type"   :options="state.option1" @change="onRefresh" />
      <van-dropdown-item v-model="state.status" :options="state.option2" @change="onRefresh" />
    </van-dropdown-menu>

    <van-pull-refresh v-model="state.refreshing" @refresh="onRefresh">
      <van-list class="list" v-model:loading="state.loading" :finished="state.finished" loading-text="正在请求数据 ..." finished-text="没有更多了" :immediate-check="false" @load="onLoad">
        <van-cell-group class="item" v-for="(item, index) in state.list" :key="index" :border="false">
          <van-cell class="line1" :title="item.remark">
            <span :style="{color: item.color}">{{item.amount}}</span>
          </van-cell>
          <van-cell class="line2" :title="item.dt" :value="'余额 ' + item.subtotal" :border="false"></van-cell>
          <van-icon class="icon" :name="item.icon" :color="item.color"></van-icon>
        </van-cell-group>
        <van-empty v-show="state.list.length == 0" :style="{width:'100%'}"></van-empty>
      </van-list>
    </van-pull-refresh>

    <van-action-sheet v-model:show="state.popup" title="卡券领取">
      <div class="form">
        <van-field type="text" :border="1" v-model="state.account" input-align="center" clearable label="券码" placeholder="请输入券码"></van-field>
        <van-button round block type="primary" :style="{margin: '10px 0'}" @click="onSubmit">领取</van-button>
      </div>
    </van-action-sheet>

  </BaseLayout>
</template>

<style scoped>
.list {
  min-height: 500px;
}
.item {
  position: relative;
  margin-top: 5px;
  max-height: 120px;
  padding-left: 40px;
  background: #FFF;
  overflow: hidden;
}
.line1 {
  height: 70px;
  font-size: 14px;
}
.line2 {
  color: #999;
  font-size: 12px;
}
.icon {
  position: relative;
  top: -90px;
  left: -30px;
  font-size: 32px;
}

.form {
  margin: 10px;
  text-align: center;
}
</style>

<script>
import {ref, reactive} from 'vue'
import BaseLayout from '@/components/BaseLayout.vue'

export default {
  components: {BaseLayout},

  setup() {

    const state = reactive({
      refreshing: false,
      loading: false,
      finished: false,

      option1: [
        {text: "全部类型", value: 0},
        {text: "满减券", value: 1},
        {text: "折扣券", value: 2},
      ],
      type: 0,

      option2: [
        {text: "所有状态", value: 0},
        {text: "未使用", value: 1},
        {text: "已使用", value: 2},
        {text: "已过期", value: 3},
      ],
      status: 0,

      list: [],
      offset: 0,
      limit : 20,

      popup: false,
      code: "",
    });

    return {state};
  },

  created() {
    this.onRefresh();
  },

  methods: {
    onRefresh() {
      this.state.popup = false;
      this.state.loading = true;
      this.state.finished = false;
      this.state.offset = 0;
      this.state.list = [];
      this.onLoad();
    },
    onLoad() {
      this.$http.get("/ticket/query?type=" + this.state.type + "&status=" + this.state.status + "&offset=" + this.state.offset + "&limit=" + this.state.limit).then((data) => {
        this.state.refreshing = false;
        if (data.length == 0 || data.length < this.state.limit)
          this.state.finished = true;
        this.state.loading = false;
        this.state.offset += data.length;

        for (let i in data){
          let item = data[i];
          this.state.list.push({...item, ...{
            amount: item.cash > 0 ? item.cash : item.point,
            icon: item.cash > 0 ? "cash-on-deliver" : "cash-back-record",
            color: (item.cash > 0 || item.point > 0) ? "#090" : "#C00",
          }});
        }
      });
    },
    onSubmit() {
      this.state.amount > 0 && this.$http.post("/ticket/gain?code=" + this.state.code).then(this.onRefresh);
    },
  }
}
</script>
